import * as THREE from 'three';
import fragment from './glsl/fragment.glsl';
import vertex from './glsl/vertex.glsl';
// import img from '../img/avatar.jpg'
// import mask from '../img/mask.jpg'
import img from '../img/header.jpg'
import mask from '../img/hMask.jpg'

const imgTexture = THREE.ImageUtils.loadTexture(img)
const maskTexture = THREE.ImageUtils.loadTexture(mask)



const Wave = () => {
  const avatarBorder = document.querySelector(".avatarBorder");
  const mainCanvas = document.getElementById("myCanvas");
  avatarBorder.style.height = mainCanvas.offsetWidth * 1.41 + 38 +'px';
  let camera, scene, renderer, material,plane
  const mobile = window.innerWidth < 600
  const width = mobile ? window.innerWidth : 600
  const canvasHeight = mobile ? window.innerWidth * 1.3 : 600 * 1.3
  function init() {
    scene = new THREE.Scene();

    renderer = new THREE.WebGLRenderer();
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setSize( width, canvasHeight);

    const container = document.getElementById('threeContainer');
    container.appendChild(renderer.domElement);

    camera = new THREE.PerspectiveCamera(
      70,
      width / canvasHeight,
      0.001, 100
    );
    camera.position.set( 0, 0, 1 );

    // controls = new OrbitControls(camera, renderer.domElement);

    imgTexture.anisotropy = renderer.getMaxAnisotropy();
    material = new THREE.ShaderMaterial( {
      side: THREE.DoubleSide,
      uniforms: {
        time: { type: 'f', value: 0 },
        pixels: {type: 'v2', value: new THREE.Vector2(width,canvasHeight)},
        mouse: {type: 'v2', value: new THREE.Vector2(0,0)},
        velocity: {type: 'f', value: 0},
        uvRate1: { value: new THREE.Vector2(1,1) },
        img: { value: imgTexture },
        mask: { value: maskTexture }
      },
      // wireframe: true,
      vertexShader: vertex,
      fragmentShader: fragment
    });

    plane = new THREE.Mesh(new THREE.PlaneGeometry( 1,1, 1, 1 ),material);
    scene.add(plane);

    resize();


  }


  let prevX = 0,prevY = 0;
  let currentX = 0,currentY = 0;

  window.addEventListener('mousemove', function(e) {
    currentX = e.clientX;
    currentY = e.clientY;
  });


  window.addEventListener('resize', resize);
  function resize() {
    const w = width;
    const h = canvasHeight;
    renderer.setSize( w, h );
    camera.aspect = w / h;

    material.uniforms.uvRate1.value.y = h / w;

    // calculate scene
    let dist  = camera.position.z - plane.position.z;
    let height = 1;
    camera.fov = 2*(180/Math.PI)*Math.atan(height/(2*dist));

    // if(w/h>1) {
    plane.scale.x = w/h;
    // }
    camera.updateProjectionMatrix();
  }

  let time = 0;
  function animate() {
    time = time+0.05;
    material.uniforms.time.value = time;

    requestAnimationFrame(animate);
    render();
  }

  function render() {
    renderer.render(scene, camera);
  }


  init();
  animate();

  function raf() {
    let speed = Math.sqrt( (currentX - prevX)*(currentX - prevX) + (currentY - prevY)*(currentY - prevY));

    speed = Math.min(speed,10);


    material.uniforms.velocity.value = speed/10;

    prevX = currentX;
    prevY = currentY;

    // console.log(speed,position);
    window.requestAnimationFrame(raf);
  }

  raf();
}

export default Wave
