import anime from "animejs";

const Menu = () => {
  let isStart = false

  const hamb = document.querySelector('.hamburger')
  const menu = document.querySelector('.menuButtons')

  const menuAnimation = anime({
    targets: '.menuButtons a',
    scale: [0.1, 1],
    opacity: 1,
    delay: anime.stagger(100),
    duration: 500,
    begin: function() {
      isStart = true
    },
    complete: function() {
      isStart = false
    }
  });
  menuAnimation.pause()
  hamb.onclick = () => {
    console.log(123123, isStart)
    if (isStart) return false
    hamb.classList.toggle("open")
    if (menu.classList.contains('open')) {
      menuAnimation.reverse();
      menuAnimation.play()
      return null
    }
    menuAnimation.play()
    menu.classList.toggle("open")
  }
}

export default Menu
