import anime from 'animejs'

const AnimationItems = () => {
  // h1
  anime({
    targets: 'h1',
    translateY: [30, 0],
    opacity: 1,
    delay: 1000,
    duration: 300,
    easing: 'easeInOutBack'
  })
  // h2

  anime({
    targets: '.fullName',
    translateY: [30, 0],
    opacity: 1,
    delay: 500,
    duration: 300,
    easing: 'easeInOutBack'
  })
}

export default AnimationItems
